@use './variables' as *;

@mixin small {
  @media only screen and (max-width: $small-width) {
    @content;
  }
}

@mixin medium {
  @media only screen and (max-width: $medium-width) {
    @content;
  }
}

@mixin large {
  @media only screen and (max-width: $large-width) {
    @content;
  }
}